<template>
  <div>
    <div id="chart" class="chart"></div>
  </div>
</template>

<script>
  import echarts from "echarts";

  export default {
    props: {
      formData: Object,
      step: Number
    },
    data() {
      return {
        chart: null
      };
    },
    computed: {
      rent() {
        return this.formData.area * this.formData.costPerArea;
      },
      laborCost() {
        return this.formData.personNumber * this.formData.costPerPerson;
      }
    },
    watch: {
      rent() {
        this.drawChart();
      },
      laborCost() {
        this.drawChart();
      },
      "formData.otherCost"() {
        this.drawChart();
      }
    },
    methods: {
      drawChart() {
        this.chart.setOption({
          title: {
            text: "成本详情",
            x: "center"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            left: "left",
            data: ["租金成本", "人力成本", "杂费成本"]
          },
          series: [
            {
              name: "成本详情",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: [
                { value: this.laborCost, name: "人力成本" },
                { value: this.formData.otherCost, name: "杂费成本" },
                { value: this.rent, name: "租金成本" }
              ]
            }
          ]
        });
      }
    },
    mounted() {
      this.chart = echarts.init(document.getElementById("chart"));
      window.addEventListener("resize", () => {
        this.chart.resize();
      });
    }
  };
</script>

<style scoped>
  .chart {
    width: 100%;
    height: 200px;
  }
</style>



