<template>
  <div class="information-form">
    <div class="basic-information" v-show="step===0">
      <Form :label-width="100">
        <h1>基本信息</h1>
        <FormItem label="名称">
          <Input v-model="formData.name"></Input>
        </FormItem>
        <FormItem label="店铺面积">
          <Slider v-model="formData.area" show-input :max="200"></Slider>
        </FormItem>
        <FormItem label="单位面积月租金">
          <InputNumber v-model="formData.costPerArea" show-input></InputNumber>
        </FormItem>
        <FormItem label="店员数">
          <Slider v-model="formData.personNumber" show-input :max="20"></Slider>
        </FormItem>
        <FormItem label="单人成本">
          <InputNumber v-model="formData.costPerPerson" show-input></InputNumber>
        </FormItem>
        <FormItem label="杂费">
          <InputNumber v-model="formData.otherCost"></InputNumber>
        </FormItem>
        <Row type="flex" justify="center">
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>
    <div class="sale-category" v-show="step===1">
      <Form :label-width="100">
        <h1>销售品类</h1>
        <FormItem label="季返基数">
          <InputNumber v-model="formData.returnValuePerQuarter"></InputNumber>
        </FormItem>
        <div class="divider"></div>
        <template v-for="(item,index) in formData.category">
          <div :key="index">
            <FormItem label="品类名称">
              <Row>
                <Col span='18'>
                <AutoComplete v-model="item.name" :data="categoryList" :filter-method="filterMethod"></AutoComplete>
                </Col>
                <Col span='6'>
                <Button class="remove-btn" @click="removeCategory(index)">remove</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem label="固返">
              <Slider v-model="item.stableReturnRate" show-input></Slider>
            </FormItem>
            <FormItem label="月返">
              <Slider v-model="item.returnRatePerMonth" show-input></Slider>
            </FormItem>
            <div class="divider"></div>
          </div>
        </template>
        <Row type="flex" justify="center">
          <Col span="12">
          <Button type="dashed" long @click="addCategory" icon="plus-round">Add category</Button>
          </Col>
        </Row>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>
    <div class="sale-rate" v-show="step===2">
      <Form :label-width="100">
        <h1>销售结构</h1>
        <FormItem label="销售额">
          <InputNumber v-model="formData.sales"></InputNumber>
        </FormItem>
        <template v-for="(item,index) in formData.category">
          <div :key="index">
            <FormItem label="品类名称">
              <Row>
                <Col span='18'>
                <span>{{item.name}}</span>
                </Col>
                <Col span='6'>
                <Button class="remove-btn" @click="removeCategory(index)">remove</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem label="毛利率">
              <Slider v-model="item.grossProfitRate" show-input></Slider>
            </FormItem>
            <FormItem label="销售占比">
              <Slider v-model="item.saleRate" show-input></Slider>
            </FormItem>
            <div class="divider"></div>
          </div>
        </template>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>
    <div class="other-information" v-show="step===3">
      <Form :label-width="100">
        <h1>其他信息</h1>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
        </Row>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      step: Number
    },
    data() {
      return {
        formData: {
          name: "默认店铺",
          area: 80,
          costPerArea: 1000,
          personNumber: 5,
          costPerPerson: 5000,
          otherCost: 10000,
          returnValuePerQuarter: 0,
          category: [
            {
              name: "",
              grossProfitRate: 0,
              stableReturnRate: 0,
              returnRatePerMonth: 0,
              saleRate: 0
            }
          ],
          sales: 0
        },
        categoryList: ["DSC", "ILC", "Lens"]
      };
    },
    methods: {
      changeStep(dir) {
        this.$emit("changeStep", dir);
      },
      changeFormData() {
        this.$emit("changeFormData", this.formData);
      },
      addCategory() {
        this.formData.category.push({
          name: "",
          grossProfitRate: 0,
          stableReturnRate: 0,
          returnRatePerMonth: 0,
          saleRate: 0
        });
      },
      removeCategory(index) {
        this.formData.category.splice(index, 1);
      },
      filterMethod(value, option) {
        return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
      }
    },
    mounted() {
      this.changeFormData();
    }
  };
</script>

<style scoped>
  h1 {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .mt-10 {
    margin-top: 20px;
  }
  .divider {
    width: 90%;
    margin: 20px auto;
    border-bottom: 2px dashed #f0f0f0;
  }
  .remove-btn {
    margin-left: 17px;
  }
</style>



